import {
  Component,
  Inject,
  OnInit,
  AfterViewInit,
  ViewChild,
  NgZone,
} from '@angular/core'
import { CommonModule, DOCUMENT } from '@angular/common'
import { FormsModule } from '@angular/forms'
import {
  IonContent,
  IonHeader,
  IonTitle,
  IonList,
  IonItem,
  IonIcon,
  IonLabel,
  IonToolbar,
  IonNote,
  IonText,
  IonButton,
  IonSkeletonText,
  IonProgressBar,
  IonThumbnail,
  IonRefresher,
  IonCard,
  IonCardContent,
  IonRow,
  IonCol,
} from '@ionic/angular/standalone'
import { HeaderComponent } from 'src/app/components/header/header.component'
import { addIcons } from 'ionicons'
import { downloadOutline } from 'ionicons/icons'
import { TranslateModule } from '@ngx-translate/core'
import {
  FactureService,
  FacturesState,
} from 'src/app/services/api-wrappers/facture.service'
import { NativeAudioService } from 'src/app/services/utils/native-audio.service'
import { ListFacture200ResponseAllOfDataInner } from 'src/api-cyclist'
import { RouterModule } from '@angular/router'
import { Browser } from '@capacitor/browser'

declare var LeaderLine: any

@Component({
  selector: 'app-invoices',
  templateUrl: './invoices.page.html',
  styleUrls: ['./invoices.page.scss'],
  imports: [
    IonContent,
    IonList,
    IonItem,
    IonIcon,
    IonLabel,
    IonNote,
    IonText,
    IonButton,
    IonCard,
    IonCardContent,
    IonSkeletonText,
    IonProgressBar,
    IonThumbnail,
    IonRefresher,
    CommonModule,
    FormsModule,
    HeaderComponent,
    TranslateModule,
    RouterModule,
    IonRow,
    IonCol,
  ],
})
export class InvoicesPage implements OnInit, AfterViewInit {
  @ViewChild(IonContent)
  content: IonContent
  invoicesState: FacturesState
  readonly skeletonCount = 10
  private leaderLine?: any

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private factureService: FactureService,
    private ngZone: NgZone,
    private nativeAudioService: NativeAudioService
  ) {
    addIcons({ downloadOutline })

    this.factureService.factureState.subscribe((state) => {
      this.invoicesState = state
    })
  }

  ngOnInit() {
    // Préchargement du son "Dring"
    this.nativeAudioService.preloadAsset(
      'bikebell',
      'assets/audio/bikebell.mp3',
      1,
      0.8
    )

    this.refresh()
  }

  ngAfterViewInit() {
    this.content.scrollEvents = true

    this.content.ionScroll.subscribe((event) => {
      const scrollTop = event.detail.scrollTop
      if (this.leaderLine) {
        this.leaderLine.position()
      }
    })

    this.factureService.factureState.subscribe(async (state) => {
      if (state.state === 'factures') {
        if (state.dues && state.dues.length > 0) {
          await setTimeout(async () => {
            this.nativeAudioService.play('bikebell')
            if (this.leaderLine) this.leaderLine.remove()
            this.leaderLine = await new LeaderLine(
              this.document.getElementById('DringFrom'),
              this.document.getElementById('DringTo'),
              {
                path: 'magnet',
                color: '#0A0A0A',
                size: 2,
                startSocket: 'left',
                endSocket: 'top',
                positionByWindowResize: true,
                hide: true,
              }
            )
            // this.document
            //   .getElementsByTagName('app-invoices')[0]
            //   .append(this.document.getElementsByClassName('leader-line')[0])
            this.leaderLine.show()
          }, 500)
        }
      }
    })
  }

  async ionViewWillLeave() {
    if (this.leaderLine) this.leaderLine.remove()
    await Browser.removeAllListeners()
  }

  handleRefresh(event: any) {
    this.refresh().then(() => {
      event.target.complete()
    })
  }

  async refresh() {
    return new Promise<void>((resolve, reject) => {
      if (this.leaderLine) this.leaderLine.hide()
      this.invoicesState = { state: 'loading' }
      this.factureService.getFactures().then(() => {
        resolve()
      })
    })
  }

  async openInAppBrowser(url: string) {
    await Browser.open({ url, presentationStyle: 'popover' })
    await Browser.addListener('browserFinished', () => {
      this.ngZone.run(() => {
        this.refresh()
      })
    })
  }
}
